<template>
  <div id="app" class="container-fluid row justify-content-md-center mx-0 px-0">
    <div class="col-12 pt-3 bg-info">
      <div v-if="error" class="alert alert-warning">
        <strong>Error: </strong> {{ error }}
      </div>

      <div class="row  justify-content-md-center">
        <div class="col-2">

        </div>
      </div>

      <div class="row">
        <div class="col">
          <form @submit.prevent="addTask">
            <div class="form-group">
              <label for="fileCSV" class="text-white">Input CSV</label>
              <input type="file" class="form-control-file text-white" id="fileCSV" @change="onFileChange">
            </div>
          </form>
        </div>
        <div class="col-lg-2 mb-3 d-none d-lg-block d-xl-block" v-if="tasks.length > 0">           
          <button type="button" @click="exportCSV(false)" class="btn btn-sm btn-block btn-warning">Export Not Sent</button>
          <button type="button" @click="exportCSV(true)" class="btn btn-sm btn-block btn-warning">Export Sent</button>
        </div>
      </div>
      <div class="row d-lg-none">
        <div class="col">
          <button type="button" @click="changeState()" class="btn btn-sm btn-block btn-default shadow-none">
            <h5 v-bind:class="[!isShowSent ? 'text-white' : 'text-muted']">Not Sent ({{notSent.length}})</h5>
            </button>
          <div  v-if="!isShowSent" class="bg-white pb-1" ></div>
        </div>
        <div class="col">
          <button type="button" @click="changeState()" class="btn btn-sm btn-block btn-default shadow-none">
            <h5 v-bind:class="[isShowSent ? 'text-white' : 'text-muted']">Sent ({{sent.length}})</h5>
          </button>
          <div v-if="isShowSent" class="bg-white pb-1" ></div>
        </div>
      </div>
    </div>

    <div class="col-lg-8 px-0 mx-0" v-bind:class="[isShowSent ? 'd-none d-lg-block d-xl-block' : '']">
      <h2 v-if="tasks.length > 0" class="d-none ml-4 mt-4 d-lg-block d-xl-block">Not Sent ({{notSent.length}})</h2>
      <ul class="list-group" v-if="notSent.length > 0" style="border-radius:0px!important">
        <li v-for="(task, id) in notSent" :key="id " class="list-group-item mx-0">
          <div class="row">
            <div class="col align-self-center">
              <p class="mb-0 text-left">{{ task.name }}</p>
              <p class="mb-0 mt-0 text-primary"><small>{{ task.phone }}</small></p>
              <p class="mb-0 mt-2 text-muted"><small>{{ task.time }}</small></p>
              <p class="mb-0 mt-0"><small>{{ task.nrp }}</small></p>
              <p class="mb-0 mt-0 text-muted"><small>#{{ task.id }}</small> <span v-if="task.failed" class="badge badge-danger">Failed</span></p>
            </div>
            <div class="col-5">
              <button type="button" @click="openMessage(task)" class="btn shadow-none btn-sm btn-block btn-primary"><small>Open Whatsapp</small></button>
              <div class="row mt-2">
                <div class="col-6 pr-1">
                  <button v-if="!task.failed" type="button" @click="mark(true, task)" class="btn shadow-none btn-sm btn-block btn-outline-success"><small>Success</small></button>
                </div>
                <div class="col-6 pl-1">
                  <button v-if="!task.failed" type="button" @click="markFailed(true, task)" class="btn shadow-none btn-sm btn-block btn-outline-danger"><small>Failed</small></button>              
                </div>
              </div>
              <button v-if="task.failed" type="button" @click="markFailed(false, task)" class="btn shadow-none btn-sm btn-block btn-danger">Cancel Failed</button>
            </div>
          </div>
        </li>
      </ul>

      <div class="card mt-2" v-if="tasks.length == 0">
        <div class="card-body">
          <h5 class="card-title">Data Kosong</h5>
          <p class="card-text">Pilih file csv untuk memulai</p>
        </div>
      </div>

    </div>

    <div class="col-lg-4 pb-3 px-0 mx-0" v-if="tasks.length > 0" v-bind:class="[!isShowSent ? 'd-none d-lg-block d-xl-block' : '']">
      <h2 class="d-none mt-4 ml-4 d-lg-block d-xl-block">Sent ({{sent.length}})</h2>
      <ul class="list-group" v-if="sent.length > 0" style="border-radius:0px!important">
        <li v-for="(task, id) in sent" :key="id" class="list-group-item" >
          <div class="row">
            <div class="col align-self-center">
              <p class="mb-0 text-left">{{ task.name }}</p>
              <p class="mb-0 mt-0 text-primary"><small>{{ task.phone }}</small></p>
              <p class="mb-0 mt-2"><small>{{ task.time }}</small></p>
              <p class="mb-0 mt-0"><small>{{ task.nrp }}</small></p>
              <p class="mb-0 mt-0"><small>#{{ task.id }} </small><span v-if="task.sent" class="badge badge-success">Sent</span></p>
            </div>
            <div class="col-5">
              <button type="button" @click="openMessage(task)" class="btn btn-sm btn-block btn-primary shadow-noney"><small>Open Whatsapp</small></button>
              <button type="button" @click="mark(false, task)" class="btn btn-sm btn-block btn-danger shadow-none"><small>Cancel Success</small></button>
            </div>
          </div>
        </li>
      </ul>
    </div>

  </div>
</template>

<script>
export default {
  name: 'app',
  data () {
    return {
      tasks: [],
      error: null,
      isShowSent: false
    }
  },
  computed: {
    notSent() {
      return this.tasks.filter((task) => !task.sent).sort((a, b) => a.id - b.id).sort((a, b) => a.failed - b.failed)
    },

    sent() {
      return this.tasks.filter((task) => task.sent).sort((a, b) => b.id - a.id)
    }
  },
  methods: {
    openMessage: function(data) {
      var link = `https://web.whatsapp.com/send?phone=${data.phone}&text=${this.genereteText(data)}`
      if (this.isMobile()) {
        link = `https://api.whatsapp.com/send?phone=${data.phone}&text=${this.genereteText(data)}`
      }

      window.open(link, '_blank').focus()
    },

    genereteText: function(data) {
    var text = `[INFORMASI JAM PELAKSANAAN VAKSINASI TAHAP DUA COVID 19]
DPP HA IPB & ARM HA-IPB Bekerjasama dengan Dinas Kesehatan Kota Bogor

Assalamualaikum Wr. Wb.
Salam Sejahtera,

@{name}
Jadwal vaksinasi tahap dua Anda adalah pada:

Hari/Tanggal: Selasa, 3 Agustus 2021
Waktu: @{time}

Tempat: Puri Begawan Jl. Raya Padjajaran Bogor 
https://goo.gl/maps/Y9WnMAzraYmYC3ns6

Harap membawa dokumen berikut pada saat kehadiran di lokasi (WAJIB):
- KTP Asli
- Fotocopy KTP
- Fotocopy KK/KIA (Usia 12-17 Tahun)
- Kartu Vaksin
- Kartu Kendali Vaksin (Unduh di: bit.ly/KKVaksin)
- Pulpen
- Wajib Memakai Masker Double (Medis dan Kain)

Diharapkan datang 15 menit sebelum waktu yang ditentukan.
Pelayanan hanya diberikan sesuai waktu yang telah ditetapkan!

SELURUH TEKNIS DAN PELAKSANAAN DI TEMPAT AKAN DIKOORDINASIKAN  OLEH DINAS KESEHATAN KOTA BOGOR.


Terima Kasih
Wassalamualaikum Wr. Wb.`

      text = text.replace("@{time}", "Waktu: " + data.time)
      text = text.replace("@{name}", data.name)
      return encodeURIComponent(text)
    },

    mark: function(isSent, data) {
        this.tasks = this.tasks.filter((task) => task.id != data.id)
        data.sent = isSent
        this.tasks.push(data)
        this.updateLocalStorage()
    },

    markFailed: function(isFailed, data) {
        this.tasks = this.tasks.filter((task) => task.id != data.id)
        data.failed = isFailed
        this.tasks.push(data)
        this.updateLocalStorage()
    },

    onFileChange: function(e) {
      const file = e.target.files[0];
      const reader = new FileReader();
      reader.onload = e => this.map(e.target.result);
      reader.readAsText(file);
    },

    map: function(res) {
      var lines=res.split("\n");
      var result = [];
      var headers=lines[0].split(",");

      for(var i=1;i<lines.length;i++){

          var obj = {};
          var currentline=lines[i].split(",");

          for(var j=0;j<headers.length;j++){
            var header = headers[j].replace(/\s+/g, ' ').trim()
            var content = currentline[j].replace(/(\r\n|\n|\r)/gm, "")
            if (header == "phone" ) {
              if (!content.startsWith("62")) {
                content = "62" + content.substring(1)
              }
            }
            obj[header] = content
          }

          if (obj.success == undefined) {
            obj.sent = false
            obj.failed = false
          }
          result.push(obj);
      }

      console.log(result)

      this.tasks = result
      this.updateLocalStorage()
    },

    exportCSV: function(isSent) {
      var tasks = []
      if (isSent) {
        tasks = this.sent
      } else {
        tasks = this.notSent
      }

      let csvContent = "data:text/csv;charset=utf-8,";
      csvContent += [
        Object.keys(tasks[0]).join(","),
        ...tasks.map(item => Object.values(item).join(","))
      ]
        .join("\n")
        .replace(/(^\[)|(\]$)/gm, "");

      const data = encodeURI(csvContent);
      const link = document.createElement("a");
      link.setAttribute("href", data);

      if (isSent) {
        link.setAttribute("download", "export-sent-data.csv");
      } else {
        link.setAttribute("download", "export-not-sent-data.csv");
      }

      link.click();
    },

    loadLocalStorage: function() {
      if(JSON.parse(localStorage.getItem('Data')) !== null &&  JSON.parse(localStorage.getItem('Data')) !== undefined) {
        this.tasks = JSON.parse(localStorage.getItem('Data'))
      }
    },
    
    updateLocalStorage: function() {
      localStorage.setItem('Data', JSON.stringify(this.tasks))
    },

    isMobile: function() {
      if(/Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(navigator.userAgent)) {
        return true
      } else {
        return false
      }
    },

    changeState: function() {
      this.isShowSent = !this.isShowSent
    }
  },
  mounted() {
    this.loadLocalStorage()
  }
}
</script>

<style>
  body {
    font-family: 'Nunito', sans-serif;
  }
  .custom-checkbox .custom-control-input.my-error~.custom-control-label::before{
    background-color:orangered;
  }
</style>



